<template>
  <div class="mortality-risk">
    <h2 class="mortality-title">Mortality Risk</h2>

    <!--   "All" -->
<div v-if="selectedModel === 'All'" class="circle-list">
  <div
    v-for="(model, index) in allModelsData"
    :key="index"
    class="circle-item"
    :style="{ backgroundColor: adjustBackgroundColor(model.percentage) }"
  >
    <!--   -->
    <div class="circle-container">
      <svg class="circle-svg" viewBox="0 0 50 50">
        <!--   -->
        <circle
          class="circle-background"
          cx="25"
          cy="25"
          r="20"
        ></circle>
        <!--   -->
        <circle
          class="circle-progress"
          cx="25"
          cy="25"
          r="20"
          :style="circleProgressStyle(model.percentage)"
        ></circle>
      </svg>
      <span class="circle-percentage">{{ model.formattedPercentage }}%</span>
    </div>

    <!--   -->
    <div class="model-details">
      <span class="model-name">{{ model.name }}</span>
    </div>
  </div>
</div>



    <!--      -->
    <div v-else class="circle-chart">
      <svg class="progress-ring" viewBox="0 0 300 300">
        <!--     -->
        <circle
          class="progress-ring__background"
          :style="innerCircleStyle(mortalityPercentage)"
          r="100"
          cx="150"
          cy="150"
        />
        <!--    -->
        <circle
          class="progress-ring__background-outer"
          stroke="#e0e0e0"
          stroke-width="15"
          fill="transparent"
          r="130"
          cx="150"
          cy="150"
        />
        <!--   -->
        <circle
          class="progress-ring__circle"
          stroke-width="15"
          fill="transparent"
          r="130"
          cx="150"
          cy="150"
          :style="outerCircleStyle(mortalityPercentage)"
        />
      </svg>
      <div class="center-content">
        <span class="percentage">{{ formattedMortalityPercentage }}%</span>
      </div>
    </div>

    <!--  ModelSelector -->
    <ModelSelector
      :selectedModel="selectedModel"
      :models="availableModels"
      @update:selectedModel="$emit('update:selectedModel', $event)"
    />
  </div>
</template>

<script>
import ModelSelector from "./ModelSelector.vue";

export default {
  name: "MortalityRisk",
  components: {
    ModelSelector,
  },
  props: {
    selectedModel: String,
    mortalityPercentage: Number, //    
    fetchMortalityRisk: Function, //    
  },
  data() {
    return {
      availableModels: ["XGBOOST", "LogisticRegression", "DecisionTree", "All"], //  All
      modelPercentages: {}, //    
    };
  },
  computed: {
    //     
    formattedMortalityPercentage() {
      return (this.mortalityPercentage * 100).toFixed(1);
    },
    //       "All"
    allModelsData() {
      return Object.entries(this.modelPercentages).map(([name, percentage]) => ({
        name,
        percentage,
        formattedPercentage: (percentage * 100).toFixed(1),
      }));
    },
  },
  methods: {
    //    
    outerCircleStyle(percentage) {
      const radius = 130;
      const circumference = 2 * Math.PI * radius;
      const offset = circumference - (percentage * circumference);
      const color = this.getColor(percentage * 100); //  -100
      return {
        strokeDasharray: `${circumference} ${circumference}`,
        strokeDashoffset: offset,
        stroke: color,
        transition: "stroke-dashoffset 1.5s ease-out, stroke 1.5s ease-out",
      };
    },
      circleProgressStyle(percentage) {
    const radius = 20; //  
    const circumference = 2 * Math.PI * radius; //  
    const offset = circumference - percentage * circumference;

    return {
      strokeDasharray: `${circumference} ${circumference}`,
      strokeDashoffset: offset,
      stroke: this.getColor(percentage * 100),
      transition: "stroke-dashoffset 1.5s ease-out, stroke 1.5s ease-out",
    };
  },
    //    
    innerCircleStyle(percentage) {
      const color = this.getColor(percentage * 100, true); //  
      return {
        fill: color,
      };
    },
    //   
    getColor(percentage, faded = false) {
      let color;
      if (percentage <= 33) color = "#4caf50"; // 
      else if (percentage <= 66) color = "#ff9800"; // 
      else color = "#f44336"; // 

      if (faded) {
        return this.adjustColorOpacity(color, 0.3);
      }
      return color;
    },
      adjustBackgroundColor(percentage) {
    const baseColor = this.getColor(percentage * 100); //     
    return this.adjustColorOpacity(baseColor, 0.1); //   10%
  },
    //   
    adjustColorOpacity(hexColor, opacity) {
      const bigint = parseInt(hexColor.replace("#", ""), 16);
      const r = (bigint >> 16) & 255;
      const g = (bigint >> 8) & 255;
      const b = bigint & 255;
      return `rgba(${r}, ${g}, ${b}, ${opacity})`;
    },
    async fetchAllModels() {
      //     
      const models = ["XGBOOST", "LogisticRegression", "DecisionTree"];
      const percentages = {};
      for (const model of models) {
        percentages[model] = await this.fetchMortalityRisk(model);
      }
      this.modelPercentages = percentages;
    },
  },
  watch: {
    selectedModel: {
      immediate: true,
      async handler(newValue) {
        if (newValue === "All") {
          await this.fetchAllModels();
        }
      },
    },
  },
};
</script>


<style scoped>
/*   */
.mortality-risk {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 30px;
  border-radius: 20px;
  max-width: 100%; /*    */
  margin: 0 auto;
}

/*  */
.mortality-title {
  font-size: 28px;
  font-weight: bold;
  color: #333;
  text-transform: uppercase;
  margin-bottom: 20px;
  text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

/*  */
.circle-chart {
  position: relative;
  width: 100%; /*    */
  max-width: 300px; /*     */
  height: auto; /*   */
  margin-bottom: 20px;
}

/*    */
.progress-ring__background {
  r: 120; /*   */
  cx: 150;
  cy: 150;
}

/*    */
.progress-ring__background-outer {
  stroke: #e0e0e0;
  stroke-width: 15;
  fill: transparent;
}

/*  -SVG */
.progress-ring {
  transform: rotate(-90deg); /* Rotate to start from top */
  width: 100%; /*    */
  height: auto; /*   */
}

/*   */
.progress-ring__circle {
  stroke-linecap: round; /*   */
  transition: stroke-dashoffset 1.5s ease-out; /*   */
}

/*    */
.center-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 90%; /*   */
  height: 90%; /*   */
  display: flex; /*   */
  justify-content: center; /*   */
  align-items: center; /*   */
  overflow: hidden; /*   */
}

.percentage {
  font-size: calc(2.5vw); /*      */
  font-weight: 900;
  color: #000;
  text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  white-space: nowrap; /*    */
  overflow: hidden; /*   */
  text-overflow: ellipsis; /*  "..."    */
  line-height: 1; /*    */
}

/*   */
.model-selector {
  margin-top: -15px;
}

.model-selector label {
  font-size: 16px;
  font-weight: bold;
  color: #333;
  margin-right: 10px;
}

.model-selector select {
  padding: 10px;
  font-size: 14px;
  border: 2px solid #ddd;
  border-radius: 5px;
  background: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.model-selector select:hover {
  border-color: #888;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

@media (max-width: 480px) {
  .circle-chart {
    max-width: 150px; /*        */
    height: auto;
  }

  .percentage {
    font-size: calc(5vw); /*       */
  }

  .mortality-title {
    font-size: 20px; /*       */
  }
}

/*    All */
.circle-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 600px;
  margin: 0 auto;
}

.circle-item {
  display: flex;
  align-items: center;
  gap: 1px;
  padding: 1px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /*   */
  transition: background-color 0.3s ease; /*    */
}

.circle-container {
  position: relative;
  width: 50px;
  height: 50px;
}

.circle-svg {
  transform: rotate(-90deg);
  width: 50px;
  height: 50px;
}

.circle-background {
  fill: transparent;
  stroke: #e0e0e0;
  stroke-width: 5;
}

.circle-progress {
  fill: transparent;
  stroke-width: 5;
  stroke-linecap: round;
  transition: stroke-dashoffset 1.5s ease-out;
}

.circle-percentage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 12px;
  font-weight: bold;
}

.model-details {
  font-size: 16px;
  font-weight: bold;
  color: #000000;
}




</style>